import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import CommentList from '../components/CommentList'
import { initComments, deleteComment } from '../reducers/comments'

// CommentListContainer
//  Smart 
//  CommentList  state
class CommentListContainer extends Component {
    static propTypes = {
        comments: PropTypes.array,
        initComments: PropTypes.func,
        onDeleteComment: PropTypes.func
    }

    componentWillMount() {
        // componentWillMount 
        this._loadComments()
    }

    _loadComments() {
        //  LocalStorage 
        let comments = localStorage.getItem('comments')
        comments = comments ? JSON.parse(comments) : []
        // this.props.initComments  connect 
        //  state 
        this.props.initComments(comments)  //mapDispatchToProps
    }
    //
    handleDeleteComment(index) {

        const { comments } = this.props
        // props 
        const newComments = [
            ...comments.slice(0, index),
            ...comments.slice(index + 1)
        ]
        //  LocalStorage
        localStorage.setItem('comments', JSON.stringify(newComments))

        if (this.props.onDeleteComment) {
            // this.props.onDeleteComment  connect 
            //  dispatch  action 
            this.props.onDeleteComment(index)  //mapDispatchToProps
        }
    }

    render() {
        return (
            <CommentList
                comments={this.props.comments}
                onDeleteComment={this.handleDeleteComment.bind(this)} />
        )
    }
}

//mapStateToProps,mapDispatchToProps,connectstate
//statereducer
//  state.comments 
const mapStateToProps = (state) => {
    return {
        comments: state.comments
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        //  CommentListContainer
        //  LocalStorage 
        //  state 
        initComments: (comments) => {
            dispatch(initComments(comments))    //reducerstate
        },
        // 
        onDeleteComment: (commentIndex) => {
            dispatch(deleteComment(commentIndex))   //reducer
        }
    }
}

//  CommentListContainer connect  store
//  commentsinitCommentsonDeleteComment  CommentListContainer
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(CommentListContainer)  


